import React from 'react';
import H3 from "../H3";
import Inner from "./Inner";
import Wrapper from "./Wrapper";

const Button = ({ href, children }) => 
  <Wrapper href={href}>
    <Inner>
      <H3 noMargin>{children}</H3>
    </Inner>
  </Wrapper>
;

export default Button;



